<template>
  <v-row dense align="center">
    <v-col cols="12">
      <v-select
        :items="aircraft"
        label="Select an Aircraft"
        prepend-icon="mdi-airplane"
      ></v-select>
    </v-col>
    <!-- <v-col cols="1">
      <v-btn
        icon
        small
        color="primary"
        v-on:click="showAutofillInformation"
      ><v-icon>mdi-information-outline</v-icon></v-btn>
    </v-col> -->
    <v-col cols="6">
      <v-autocomplete
        :items="airports"
        label="Where from?"
        prepend-icon="mdi-map-marker"
        v-model="homeAirportCode"
        @change="checkIfAutocompleteAvailableHome()"
      ></v-autocomplete>
    </v-col>
    <v-col cols="6">
      <v-autocomplete
        :items="airports"
        label="Where to?"
        prepend-icon="mdi-map-marker"
        v-model="destAirportCode"
        @change="checkIfAutocompleteAvailableDest()"
      ></v-autocomplete>
    </v-col>
    <v-col cols="3">
      <v-btn
        small
        block
        :disabled="this.disableAutoUpdateHome"
        color="primary"
        v-on:click="autoUpdateFieldsHome"
      >Autofill</v-btn>
    </v-col>
    <v-col cols="3">
      <v-btn
        small
        block
        :disabled="!this.homeAirportCode && !this.homeTemperature && !this.homeElevation && !this.homeWindSpeed"
        color="primary"
        v-on:click="clearAllFieldsHome"
      >Clear</v-btn>
    </v-col>
    <v-col cols="3">
      <v-btn
        small
        block
        :disabled="this.disableAutoUpdateDest"
        color="primary"
        v-on:click="autoUpdateFieldsDest"
      >Autofill</v-btn>
    </v-col>
    <v-col cols="3">
      <v-btn
        small
        block
        :disabled="!this.destAirportCode && !this.destTemperature && !this.destElevation && !this.destWindSpeed"
        color="primary"
        v-on:click="clearAllFieldsDest"
      >Clear</v-btn>
    </v-col>
    <v-col cols="6">
      <v-text-field
        v-model="homeTemperature"
        label="Temperature (Celsius)"
        prepend-icon="mdi-thermometer"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6">
      <v-text-field
        v-model="destTemperature"
        label="Temperature (Celsius)"
        prepend-icon="mdi-thermometer"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6">
      <v-text-field
        v-model="homeElevation"
        label="Elevation (meters)"
        prepend-icon="mdi-elevation-rise"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6">
      <v-text-field
        v-model="destElevation"
        label="Elevation (meters)"
        prepend-icon="mdi-elevation-rise"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6">
      <v-text-field
        v-model="homeWindSpeed"
        label="Wind Speed (kt)"
        prepend-icon="mdi-weather-windy"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6">
      <v-text-field
        v-model="destWindSpeed"
        label="Wind Speed (kt)"
        prepend-icon="mdi-weather-windy"
        hide-details
        type="number"
      />
    </v-col>
  </v-row>
</template>

<script>

import axios from 'axios'
axios.defaults.withCredentials = false

export default {
  name: 'Plan',

  data: () => ({
    homeMetarData: {},
    homeAirportCode: undefined,
    homeTemperature: undefined,
    homeWindSpeed: undefined,
    homeElevation: undefined,
    destMetarData: {},
    destAirportCode: undefined,
    destTemperature: undefined,
    destWindSpeed: undefined,
    destElevation: undefined,
    disableAutoUpdateHome: true,
    disableAutoUpdateDest: true,
    aircraft: [],
    airports: []
  }),

  created: function () {
    this.loadAirportCodes()
  },

  methods: {

    parseMetarXml: function (data) {
      const json = {}
      const xml = data.getElementsByTagName('METAR')[0]
      if (!xml) {
        return json
      }
      json.latitude = Number(xml.getElementsByTagName('latitude')[0].innerHTML)
      json.longitude = Number(xml.getElementsByTagName('longitude')[0].innerHTML)
      json.tempC = Number(xml.getElementsByTagName('temp_c')[0].innerHTML)
      json.windSpeedKt = Number(xml.getElementsByTagName('wind_speed_kt')[0].innerHTML)
      json.elevationM = Number(xml.getElementsByTagName('elevation_m')[0].innerHTML)
      json.observationTime = new Date(xml.getElementsByTagName('observation_time')[0].innerHTML)
      return json
    },

    parseAirportCodes: function (data) {
      const codes = []
      const xml = data.getElementsByTagName('Station')
      for (let i = 0; i < xml.length; i++) {
        const stationXml = xml[i]
        const stationId = stationXml.getElementsByTagName('station_id')[0].innerHTML
        const stationSite = stationXml.getElementsByTagName('site')[0].innerHTML
        const stationState = stationXml.getElementsByTagName('state')[0].innerHTML
        codes.push({
          value: stationId,
          text: stationSite + ', ' + stationState + ' (' + stationId + ')'
        })
      }
      return codes
    },

    autoUpdateFieldsHome: function () {
      this.homeTemperature = this.homeMetarData.tempC
      this.homeElevation = this.homeMetarData.elevationM
      this.homeWindSpeed = this.homeMetarData.windSpeedKt
    },

    clearAllFieldsHome: function () {
      this.homeAirportCode = undefined
      this.homeTemperature = undefined
      this.homeElevation = undefined
      this.homeWindSpeed = undefined
    },

    autoUpdateFieldsDest: function () {
      this.destTemperature = this.destMetarData.tempC
      this.destElevation = this.destMetarData.destElevation
      this.destWindSpeed = this.destMetarData.windSpeedKt
    },

    clearAllFieldsDest: function () {
      this.destAirportCode = undefined
      this.destTemperature = undefined
      this.destElevation = undefined
      this.destWindSpeed = undefined
    },

    loadAirportCodes: function () {
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=stations&requestType=retrieve&format=xml&stationString=~us', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          const response = this.parseAirportCodes(data)
          this.airports = response
        })
    },

    showAutofillInformation: function () {
      console.log('info')
    },

    checkIfAutocompleteAvailableHome: function () {
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=metars&requestType=retrieve&format=xml&stationString=' + this.homeAirportCode + '&hoursBeforeNow=12&mostRecent=true', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          if (data !== undefined) {
            const response = this.parseMetarXml(data)
            if (Object.keys(response).length > 0) {
              this.homeMetarData = response
              this.disableAutoUpdateHome = false
            } else {
              this.disableAutoUpdateHome = true
            }
          }
        })
    },

    checkIfAutocompleteAvailableDest: function () {
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=metars&requestType=retrieve&format=xml&stationString=' + this.destAirportCode + '&hoursBeforeNow=12&mostRecent=true', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          if (data !== undefined) {
            const response = this.parseMetarXml(data)
            if (Object.keys(response).length > 0) {
              this.destMetarData = response
              this.disableAutoUpdateDest = false
            } else {
              this.disableAutoUpdateDest = true
            }
          }
        })
    }

  }

}
</script>
